document.addEventListener('DOMContentLoaded', function() {
    mostrarBranch();
});

async function mostrarBranch(){
    try{
        const resultado = await fetch('/api/surcursal/obtener',{method: 'POST'});
        const sucursales = await resultado.json();
        console.log(sucursales);
        sucursales["sucursal"].forEach(sucursal => {
            const { location, name } = sucursal;
            const contenido = document.querySelector(".sucursal-contenido");
            const titulo = document.querySelector(".nombre");
            const info = document.querySelector(".info");
            const mapa = document.querySelector(".mapa");

            // Titulo
            const h1 = document.createElement("h1");
            h1.textContent = name;
            titulo.appendChild(h1);
            contenido.appendChild(titulo);

            // Informacion
            const parrafo = document.createElement("p");
            const span = document.createElement("span");
            parrafo.textContent = "Direccion: ";
            span.textContent = location;
                
            parrafo.appendChild(span);
            info.appendChild(parrafo);
            contenido.appendChild(info);

            // Mapa
            const img = document.createElement("img");
            img.textContent = "hola";
            mapa.appendChild(img);
            contenido.appendChild(mapa);
        });
    }
    catch(e){
        console.error(e);
    }
}